#hackathon {
    background-color: var(--accent);
    color: var(--light2);
    display: flex;
}

#hackathon .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 10vh;
    padding-bottom: 10vh;
}

#hackathon .info {
    display: flex;
    flex-direction: row;
}

#hackathon .info .details {
    width: 30vw;
}

#hackathon .detailsTitle {
    text-align: center;
}

#hackathon ul {
    list-style-type: none;
    padding-top: 2vh;
    font-size: 1.2rem;
}

#hackathon li {
    padding: 0.5vh;
}

#hackathon .button {
    justify-content: flex-start;
    margin: 0;
}

#hackathon .button-link:not(:hover) {
    background-color: var(--light2);
    color: var(--accent);
}
