#NavBar {
    display: flex;
    top: 0;
    width: 100%;
    height: 10vh;
    align-items: center;
    justify-content: space-between;
}

#logo {
    height: 70px;
    float: left;
    padding-left: 20px;
}

.menuItems {
    display: flex;
    padding: 10px;
}

.menuItem {
    padding: 2.5vh;
    justify-content: center;
    color: var(--light2);
    text-align: center;
}

.menuItem:hover {
    background-color: grey;
    color: var(--light2);
    text-decoration: none;
    border-radius: 4px;
    transition-duration: 0.3s;
}

#menu {
    width: 100%;
    padding-right: 50px;
}

#menu-items {
    float: right;
}

#floating-menu {
    background-color: var(--dark2);
    position: fixed;
    z-index: 4;
}

#mobile-menu-hamburger {
    color: var(--light2);
    padding: 30px 30px 0;
    justify-content: center;
    vertical-align: middle;
    font-size: 20px;
}
