#partners {
    background-color: var(--light2);
    color: var(--dark2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 140px 0;
    letter-spacing: 2px;
}

#partners .title {
    text-transform: uppercase;
}

.sponsorList {
    display: flex;
    justify-content: space-around;
    padding: 50px;
    flex-wrap: wrap;
}

.sponsor {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.sponsorImage {
    height: 70px;
    width: auto;
}

#partners .button {
    margin: 10px;
    padding: 2px;
}
