@media (min-width: 1100px) {
    #mobile-menu-hamburger {
        display: none;
    }

    #homeItem {
        display: none;
    }
}

@media (max-width: 1100px) {
    #NavBar:not(.hidden) {
        position: relative;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        background: var(--dark2);
        z-index: 10;
        width: 300px;
        height: 100vh;
        float: right;
    }

    #NavBar:not(.hidden) + #first-page {
        height: 100vh;
    }

    #NavBar:not(.hidden) .page {
        width: 70vw;
    }

    #NavBar .hidden + #mobile-menu-hamburger {
        padding-bottom: 10px;
    }

    #floating-menu {
        padding: 0;
    }

    #floating-menu .hidden {
        background-color: var(--dark2);
    }

    #floating-menu:not(.hidden) {
        background-color: transparent;
    }

    .logo:not(.hidden) {
        display: none;
    }

    .menuItems.hidden {
        display: none;
    }

    .menuItems {
        flex-direction: column;
    }

    #about-us .box {
        width: 90vw;
    }
}

@media (max-width: 768px) {
    #NavBar:not(.hidden) {
        position: relative;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        background: var(--dark2);
        z-index: 10;
        width: 100vw;
    }

    #logo {
        height: 50px;
    }

    .box {
        padding: 6vw;
    }

    .title {
        font-size: 8vw;
    }

    #subtitle {
        font-size: 4vw;
    }

    .subtitle {
        font-size: 4vw;
    }

    .button-link {
        font-size: 2.5vw;
    }

    #hackathon h4 {
        font-size: 3vw;
    }

    #workshops h4 {
        font-size: 3vw;
    }

    #conferences h4 {
        font-size: 3vw;
    }

    #winners h4 {
        font-size: 3vw;
    }

    .vertical-timeline-element-title {
        font-size: 20px;
    }

    .vertical-timeline-element-content p {
        font-size: 15px;
    }

    #partners h4 {
        font-size: 3vw;
        padding: 2vw;
        text-align: center;
    }
}

@media (max-width: 650px) {
    #contact {
        justify-content: center;
    }

}

@media (max-width: 450px) {
    .membersList {
        padding: 2vw;
    }

    .memberImage {
        width: 70vw;
    }

    .memberName {
        font-size: 5vw;
    }

    .contactLink {
        font-size: 4vw;
        padding: 1vw;
    }

    .collaboratorName {
        font-size: 3.5vw;
    }

    .sponsorImage {
        height: 7vh;
    }

    .logoUB {
        width: 50vw;
        height: auto;
    }
}
