#home {
    background: url('../../images/fundal-home-page.jpg') no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

#first-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
}

#home .title {
    text-align: left;
}

#subtitle {
    font-size: 2rem;
    text-align: center;
}

#first-page.button {
    background: var(--accent);
}
