#conferences {
    background-color: var(--accent);
    color: var(--light2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 80px;
    padding-top: 10vh;
    padding-bottom: 10vh;
}

#conferences ul {
    list-style-type: none;
    padding-top: 2vh;
    font-size: 1.2rem;
}

#conferences li {
    padding: 0.5vh;
}

#conferences .button {
    justify-content: flex-start;
    margin: 0;
}

#conferences .button-link:not(:hover) {
    background-color: var(--light2);
    color: var(--accent);
}
